import "./Style.css";
import React from "react";
import { Link } from "react-scroll";
import { useNavigate } from "react-router-dom";

export default function Header() {
  const movePage = useNavigate();

  function goLike() {
    movePage("/Like");
  }
  function goDislike() {
    movePage("/Dislike");
  }
  function goHobby() {
    movePage("/Hobby");
  }

  return (
    <div className="Header">
      <h1 style={{ fontSize: "40px" }}>
        <br />
        <center></center> <br />
      </h1>
      <nav>
        <ul className="menu">
          <li>
            <a href="/web/Like" onClick={goLike}>
              
            </a>
          </li>
          <li>
            <a href="/web/Dislike" onClick={goDislike}>
              
            </a>
          </li>
          <li>
            <a href="/web/Hobby"></a>
            <ul className="submenu">
              <li>
                <a href="/Hobby" onClick={goHobby}>
                  
                </a>
              </li>
              <li>
                <Link
                  to="section1"
                  spy={true}
                  smooth={true}
                  duration={1200}
                  offset={-150}
                >
                  
                </Link>
              </li>
              <li>
                <Link
                  to="section2"
                  spy={true}
                  smooth={true}
                  duration={1200}
                  offset={-10}
                >
                  
                </Link>
              </li>
            </ul>
          </li>
          <li>
            <a href="/web">Main</a>
          </li>
        </ul>
      </nav>
    </div>
  );
}

